.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.game-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  margin-top: 40px;
}

.player-section {
  padding: 20px;
  border: 2px solid #61dafb;
  border-radius: 10px;
  width: 300px;
}

.card-space {
  height: 200px;
  width: 140px;
  border: 2px dashed #61dafb;
  margin: 20px auto;
  border-radius: 10px;
}

.draw-button {
  padding: 15px 30px;
  font-size: 1.2em;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  color: #282c34;
  cursor: pointer;
  transition: background-color 0.3s;
}

.draw-button:hover {
  background-color: #4fa8d3;
}

.game-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
